.header {
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  max-width: 100%;
  padding: 0 50px;
  display: flex;
}

.header__img {
  width: 40px;
  height: 40px;
  display: block;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  height: 15px;
  padding: 22.5px 0;
  font-size: 1em;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__list .nav__item:not(:first-child) {
  margin-left: 20px;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.card-container {
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

.card {
  box-sizing: border-box;
  cursor: pointer;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  padding: 16px;
  transition: all .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 16px auto 0;
}

.card__title {
  color: #060b35;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
  transition: all .3s;
}

.card__code {
  color: #616070;
  margin: 4px 0 0;
  font-size: 10px;
  line-height: 14px;
}

.card__details {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
}

.card__price-wrapper {
  color: #616070;
  justify-content: space-between;
  margin: 24px 0 16px;
  display: flex;
}

.card__price-text {
  margin: 0;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  margin: 0;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card:hover {
  transform: scale(1.2);
}

.card__link {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 0;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  padding: 12px 11.5px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__link:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars {
  box-sizing: border-box;
  justify-content: space-between;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars__star:first-child, .stars__star:nth-child(2), .stars__star:nth-child(3), .stars__star:nth-child(4) {
  background-image: url("star-active.edda4c43.svg");
}

@media (width >= 488px) {
  .card-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .card-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .card-container {
    grid-template-columns: repeat(4, 200px);
  }
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

